body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-container {
  align-items: center;
  background-color: #323740;
  display: flex; 
  height: 100%;
  justify-content: center; 
  width: 100%; 
}

form {
  align-items: center;
  background-color: white; 
  display: flex; 
  flex-direction: column; 
  justify-content: center;  
  width: 400px;
}

form * {
  height: 40px; 
  margin-bottom: 20px; 
  width: 200px;   
}

.inner-container {
  background-color: white;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  box-shadow: 2px 2px 10px black;
  display: flex; 
  flex-direction: column; 
  height: 450px;  
  justify-content: center; 
}

.message-container {
  background: white;
  text-align: center;
}

.popup-message {
  font-family: 'Lato', sans-serif;
}

input {
  background: transparent;
  border: none;
  border-bottom: 3px solid grey;
  font-family: 'Lato', sans-serif;
}

input:focus {
  border: 3px solid transparent;
  border-left: none;
  border-right: none;
  border-top: none;
  -webkit-border-image: -webkit-linear-gradient(left, rgba(199, 251, 140, 1), rgba(118, 225, 239, 1), rgba(50, 54, 64, 1), rgba(118, 225, 239, 1), rgba(199, 251, 140, 1)) 1 100%;
          border-image: linear-gradient(to right, rgba(199, 251, 140, 1), rgba(118, 225, 239, 1), rgba(50, 54, 64, 1), rgba(118, 225, 239, 1), rgba(199, 251, 140, 1)) 1 100%;
  border-image-slice: 1;
  font-size: 18px;
  outline: none;
}

input:focus::-webkit-input-placeholder {
  font-family: 'Lato', sans-serif;
  font-size: 18px;
}

input:focus::-ms-input-placeholder {
  font-family: 'Lato', sans-serif;
  font-size: 18px;
}

input:focus::placeholder {
  font-family: 'Lato', sans-serif;
  font-size: 18px;
}

input:-webkit-autofill:active  {
  -webkit-box-shadow: 0 0 0 30px #76E1EF inset;
}

.submit-sign-up {
  background: -webkit-linear-gradient(left, rgba(118, 225, 239, 1), rgba(199, 251, 140, 1));
  background: linear-gradient(to right, rgba(118, 225, 239, 1), rgba(199, 251, 140, 1));
  border-radius: 7px;
  border: none;
  box-shadow: 2px 2px 15px black;
  font-family: 'Lato', sans-serif;
  font-size: 17px;
  margin-bottom: 0px;
  outline: none;
  cursor: pointer;
}

.submit-sign-up:hover {
  background: -webkit-linear-gradient(left, rgba(199, 251, 140, 1), rgba(118, 225, 239, 1));
  background: linear-gradient(to right, rgba(199, 251, 140, 1), rgba(118, 225, 239, 1));;
}

.signin-toggle {
  background: none;
  border: none;
  font-size: 12px;
  margin-top: 0px;
  outline: none;
  cursor: pointer;
}

.signin-toggle:hover {
  text-decoration: underline;
}

