.App {
  background-color: #323740;
  font-family: 'Exo', sans-serif;
  height: 100vh; 
}

.movie-card {
  border: 5px solid #8E959C;
  display: flex;
  height: 300px;
  margin-bottom: 25px;
  padding-right: 10px;
  width: 500px;
}

.movie-img {
  height: 300px;
  margin-right: 20px;
  width: 185px;
}

.movie-info-text {
  width: 100%;
}

.movie-icon-container {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}

.movie-rating-chart {
  height: 50px;
  padding-top: 12px;
  width: 50px;
}

.outline-circle,
.circle {
  stroke-width: 3;
}

.outline-circle {
  fill: #8E959C;
  stroke: #fff;
}

.circle {
  fill: none;
  -webkit-animation: progress 4s ease-out forwards;
          animation: progress 4s ease-out forwards;
  stroke: #76E1EF;
  stroke-linecap: round;
}

@-webkit-keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }

  60% {
    stroke-dasharray: 100 100;
  }
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }

  60% {
    stroke-dasharray: 100 100;
  }
}

.movie-rating {
  font-size: 0.8rem;
}

.popcorn-icon {
  height: 50px;
  align-self: flex-end;
  cursor: pointer;
}

.movie-title, 
.movie-release {
  padding-left: 5px;
}

.movie-title {
  color: #76E1EF;
  font-size: 1.5rem;
  margin: 0;
}

.movie-release,
.movie-description {
  color: #8E959C;
}

.movie-description {
  border-radius: 10px;
  cursor: default;
  overflow: auto;
  width: 200px;
  height: 100px;
  -webkit-transition: box-shadow 500ms;
  transition: box-shadow 500ms;
  padding: 5px;
}

.movie-description:hover {
  box-shadow: inset 0 -45px 130px -55px rgba(0, 0, 0, 1);
}


.movie-container {
  background-color: #323740;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 25px;
}

.favorites-error {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.server-error, .loading {
  width: 100%;
  text-align: center;
}
.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  border-bottom: 6px solid #3D414C;
}

.header-title-navlink {
  text-decoration: none;  
}

.header-title {
  color: #76E1EF;
  font-size: 4rem;
  margin: 0;
  margin-left: 30px;
}

.login-btn {
  background-color: #76E1EF;
  border-radius: 15px;
  font-size: 1.3rem;
  height: 40px;
  margin-right: 30px;
  width: 120px; 
  cursor: pointer;
}

.dropdown-title {
  margin-right: 40px;
  position: relative;
}

.dropdown-title * {
  display: inline-block;
}

.dropdown-list {
  align-items: center;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 80px;
  margin-top: -15px;
  padding: 10px;
  position: absolute;
  width: 103px;
}

.favorites-btn,
.home-btn,
.logout-btn {
  border: none;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  width: 100%;
  cursor: pointer;
}

.favorites-btn:hover,
.home-btn:dhover,
.logout-btn:hover {
  cursor: pointer;
  color: #76E1EF;
  text-shadow: 1px 1px #3D414C;
  cursor: pointer;
  text-decoration: underline;
} 

.arrow-icon {
  height: 10px;
  width: auto;
}

.welcome-user {
  cursor: pointer;
  color: #76E1EF;
  margin-right: 10px;
}
